import react, { Component } from 'react';

import {
    isBrowser,
    isMobile
} from "react-device-detect";

class Index extends Component {
    render() {
        let title;
        if(isBrowser) {
            title= <div>Welcome to Next.js on Browser</div>
        }
        if(isMobile) {
            title= <div>Welcome to Next.js</div>
        }
        return (
            <div>Welcome to Next.js</div>
        );
    }
}

export default Index;
